.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  background-color: rgba($color-default-black, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;

  transition: opacity $trans-modal,
    transform $trans-modal,
    visibility $trans-modal;

  pointer-events: none;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  &__wrapper {
    position: relative;

    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 60px 40px;

    @include vp-767 {
      padding: 60px 16px;
    }
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    cursor: pointer;
  }

  &__content {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 500px;
    height: 610px;
    margin: auto;
    padding: 40px;
    background-color: $color-light-blue;
    border-radius: 3px;
    color: $color-default-white;
  }

  &__close-btn {
    position: absolute;
    top: 22px;
    right: 22px;
    z-index: 1;

    width: 40px;
    height: 40px;
    padding: 0;

    background: $color-transparent;
    border: none;
    cursor: pointer;
  }

  &--responsive {
    .modal__content {
      width: 100%;
    }
  }

  &--fit-content {
    .modal__content {
      width: auto;

      @include vp-767 {
        width: 100%;
      }
    }
  }

  &--no-scale {
    transform: none;
  }

  &--preload {
    transition: none;
  }

  &.is-active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;

    pointer-events: auto;
  }

  @include vp-1023 {
    display: none;
  }
}

.modal--error {
  animation: popup-shake 0.6s;
}

.modal__close-button {
  position: absolute;
  top: 20px;
  right: 18px;
  background-color: transparent;
  border: none;
  width: 30px;
  height: 30px;
  padding: 5px;
  fill: $color-default-white;
  cursor: pointer;

  &:hover {
    fill: $color-dark-grey;
    outline: 1px solid $color-dark-grey;
    border-radius: 3px;
  }

  &:active {
    outline: 1px solid $color-light-grey;
    opacity: 0.6;
  }
}
